/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.eltdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.eltdf-content .woocommerce {
    @include eltdfRelativeHolderLayout();
}

.eltdf-pl-main-holder {
    margin: 0 0 30px;
    
    .eltdf-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;
    }
}

ul.products {
    list-style: none;
    margin: 0 -10px;
    clear: both;
    
    > .product {
        position: relative;
        float: left;
        display: inline-block;
        vertical-align: middle;
        padding: 0 10px;
        margin: 0 0 20px;
        box-sizing: border-box;
        
        .touch & {
            cursor: pointer;
        }

        &:hover {
            .eltdf-pl-inner .eltdf-pl-text {
                opacity: 1;
            }

            .eltdf-pl-add-to-cart{
                opacity: 1;
            }

            .eltdf-pl-text-inner {
                @include eltdfTransform(translateY(0));
            }
        }
        
        .eltdf-product-list-title {
            margin: 5px 0;
            text-transform: uppercase;
        }
        
        .eltdf-pl-rating-holder {
            @include eltdfRelativeHolderLayout();
            line-height: 1;
            margin: 3px 0 6px;
        }
        
        .eltdf-pl-text-outer {
            overflow: hidden;
        }

        .eltdf-pl-text-inner {
            @include eltdfTransition(all .2s);
            @include eltdfTransform(translateY(100%));
        }

        .price {
            display: inline-block;
            line-height: 1em;
            @include eltdfTransition(all 0.3s ease);
            will-change: transform;
            
            * {
                font-size: inherit;
                line-height: inherit;
            }
            
            del {
                margin: 0 8px 0 0;
            }
            
            ins {
                text-decoration: none;
            }
        }

        .eltdf-pl-add-to-cart{
            font-size: 11px;
            font-weight: 400;
            text-transform: uppercase;
            position: absolute;
            top: 50%;
            left: 50%;
            @include eltdfTransform(translate(-50%, -50%));
            letter-spacing: 2px;
            opacity: 0;
            @include eltdfTransition(all 0.3s ease);
            display: inline-block;
            background-color: rgba(255,255,255,0.8);
            padding: 15px 25px;

            a{
                color: $default-heading-color;
                white-space: nowrap;

                &.eltdf-read-more-button{
                    position: relative;
                    display: block;
                }

                .eltdf-add-to-cart-icon {
                    display: inline-block;
                    vertical-align: middle;
                    margin-left: 9px;
                    font-size: 25px;
                }
            }
        }
        
        .button,
        .added_to_cart {
            margin: 16px 0 0;
        }
        
        .added {
            display: none;
        }
        
        .eltdf-pl-inner {
            @include eltdfRelativeHolderLayout();
            overflow: hidden;
            
            .eltdf-pl-image {
                @include eltdfRelativeHolderLayout();
                overflow: hidden;
                
                img {
                    display: block;
                    width: 100%;
                }
            }
            
            .eltdf-pl-text {
                position: absolute;
                bottom: 0;
                display: inline-block;
                opacity: 0;
                width: 100%;
                box-sizing: border-box;
                @include eltdfTransition(opacity .2s ease-out, background-color .2s ease-out);
                z-index: 10;
                white-space: nowrap;
            }
            
            .eltdf-pl-text-inner {
                font-size: 0;
            }
        }
        
        .eltdf-pl-text-wrapper {
            @include eltdfRelativeHolderLayout();
            margin: 11px 0;
            text-align: center;
        }
    }
}

.eltdf-woo-pl-hide-ratings ul.products>.product .eltdf-pl-rating-holder {
    display: none;
}

.eltdf-woo-pl-info-on-image-hover {
    ul.products > .product {

        .eltdf-pl-inner .eltdf-pl-text {
            padding: 10px;
            background-color: rgba(255, 255, 255, 0.8);
            @include eltdfTransform(none);
            height: 100%;
            width: 100%;
            left: 0;
            top: 0;
            z-index: 8;

            .eltdf-pl-text-outer {
                position: relative;
                display: table;
                width: 100%;
                height: 100%;
            }

            .eltdf-pl-text-inner {
                position: relative;
                display: table-cell;
                width: 100%;
                height: 100%;
                vertical-align: middle;
                text-align: center;
                font-size: 15px;
                line-height: 1em;
                @include eltdfTransform(none);
            }
        }

        .eltdf-pl-image img {
            @include eltdfTransition(all .4s ease);
        }

        .add_to_cart_button {
            display: none;
        }

        .eltdf-pl-inner > a {
            @include eltdfAbsoluteHolderLayout();
            z-index: 10;
        }

        &:hover{
            .eltdf-pl-text {
                @include eltdfTransition(all .6s cubic-bezier(0.12, 0.68, 0.43, 0.95));
                opacity: 1;
            }

            .eltdf-pl-image img {
                @include eltdfTransform(scale(1.05));
            }
        }
    }
}

$columns_space_label: ('no', 'small', 'normal');
$columns_space_width: (0, 10, 15);

@for $i from 0 to length($columns_space_label) {
    &.eltdf-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.eltdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .eltdf-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}

@include ipad-landscape {
    .eltdf-pl-holder.eltdf-standard-layout.eltdf-four-columns .eltdf-pli {
        width: 50% !important;
        float: none;
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */