/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - begin
   ========================================================================== */

.woocommerce {
    
    .eltdf-onsale,
    .eltdf-out-of-stock,
    .eltdf-new-product {
        position: absolute;
        display: block;
        top: 15px;
        right: 15px;
        width: 58px;
        height: 58px;
        border-radius: 100%;
        font-family: $alternative-text-font;
        font-size: 18px;
        line-height: 58px;
        font-style: italic;
        text-align: center;
        z-index: 100;
        @include eltdfTransform(translateZ(0));
        box-sizing: border-box;
    }

    .eltdf-out-of-stock,
    .eltdf-new-product {
        background-color: $first-main-color;
        color: #fff;
    }

    .eltdf-onsale {
        background-color: $first-main-color;
        color: #fff;
    }
    
}

/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - end
   ========================================================================== */