ul.products {
    > .product{
        > .yith-wcqv-button{
            display: none !important;
        }
    }
}

%yith-wcqv-button-style {
    .eltdf-yith-wcqv-holder {
        display: inline-block;
        vertical-align: middle;
        height: 35px;
        width: 50%;
        line-height: 33px;
        background-color: $first-main-color;
        text-align: center;
        border-right: 1px solid #616161;

        .yith-wcqv-button {
            position: relative;
            display: inline-block;
            width: 100%;
            height: 100%;
            color: #fff;
            font-size: 12px;
            font-weight: 400;
            letter-spacing: 0.1em;
            line-height: inherit;

            div {
                background-size: 15px 15px !important;
            }

             &:after {
                content: "H";
                position: relative;
                display: inline-block;
                vertical-align: top;
                font-family: "linea-basic-10";
                font-size: 20px;
                padding-left: 5px;
                line-height: inherit;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                @include eltdfTransition(opacity .2s);
            }

        }
    }
}

.eltdf-pl-holder .eltdf-pli-inner .eltdf-pli-text-inner,
.eltdf-plc-holder .eltdf-plc-item .eltdf-plc-text-inner,
ul.products > .product .eltdf-pl-inner .eltdf-pl-text-inner{
    @extend %yith-wcqv-button-style;
}