/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.eltdf-woo-single-page {

	&.eltdf-woo-single-thumb-on-left-side {
		
		.eltdf-single-product-content {
			
			.images {
				
				a {
					
					&.woocommerce-main-image {
						width: 81%;
						margin: 0 0 0 19%;
					}
				}

				.eltdf-new-product{
					margin-left: 19%;
				}
				
				.thumbnails {
					margin: 0;
					
					a {
						margin: 0;
						padding: 16px 0 0;
						
						&:first-child {
							padding: 0;
						}
					}
					
					&.columns-4 {
						position: absolute;
						top: 0;
						left: 0;
						display: inline-block;
						vertical-align: top;
						width: 16%;
						height: 100%;
						
						a {
							float: none;
							width: 100%;
						}
					}
				}

				@media only screen and (min-width: $ipad-portrait-plus-pixel) {

					figure {
						width: 100%;
						margin: 0;
					}

					.woocommerce-product-gallery__image {

						&:first-of-type {
							left: 120px;
                            width: calc(100% - 120px);
						}
					}

					&.woocommerce-product-gallery--with-images {

						.woocommerce-product-gallery__image {

							&:not(:first-of-type) {
								    position: absolute;
								    top: 0;
								    left: 0;
								    width: 115px;
								    height: calc(25% + 5px);
								    padding: 0 0 20px;
								    box-sizing: border-box;

								a {
									height: 100%;

									img {
										height: 100%;
									}
								}
							}

							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
                            @for $i from 0 to length($thumbnail_numbers) {
                                $thumbnail_number_value: nth($thumbnail_numbers, $i+1);

                                &:nth-of-type(#{$thumbnail_number_value}#{n}) {
                                    $first_param: (25 * $i)#{'%'};
                                    $second_param: (5 * $i)#{px};
                                    $topPos: calc(#{$first_param} + #{$second_param});

                                    top: $topPos;
                                }
                            }
						}
					}
				}

			}
		}
	}
    
    .product {
	    @include eltdfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }

	.woocommerce-message,
	.woocommerce-error{
		padding: 10px 30px 12px;
        margin: -13px 0 35px;
	}

	.woocommerce-message:nth-of-type(2n),
	.woocommerce-message:nth-of-type(2n+1){
		border-top-width: 0;
	}

	.woocommerce-message:first-of-type{
		border-top-width: 1px;
	}

	.product-type-grouped {
		ins {
			text-decoration: none;
		}
	}

    .eltdf-single-product-content {
        @include eltdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
		background-color: #fff;
		margin: 0 -1000px;
		padding: 0px 1000px 142px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 50%;
		    margin: 0;
		    padding: 0;
		    float: left;
		    box-sizing: border-box;
		
		    a {
			    @include eltdfRelativeHolderLayout();
			
			    img {
				    position: relative;
				    width: 100%;
				    display: block;
				    z-index: 1;
				
				    &.eltdf-fake-featured-image {
					    position: absolute;
					    top: 0;
					    left: 0;
					    z-index: 2;
						height: 100%;
				    }
			    }
		    }
		
		    .thumbnails {
			    margin: 0 -7px;
			
			    a {
				    margin: 14px 0 0;
				    padding: 0 7px;
				    box-sizing: border-box;
			    }
			
			    &.columns-2 {
				
				    a {
					    float: left;
					    width: 50%;
				    }
			    }
			
			    &.columns-3 {
				
				    a {
					    float: left;
					    width: 33.3333333%;
				    }
			    }
			
			    &.columns-4 {
				
				    a {
					    float: left;
					    width: 25%;
				    }
			    }
		    }

			figure {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0 -10px;

				a {
					@include eltdfRelativeHolderLayout();

					img {
						position: relative;
						width: 100%;
						display: block;
					}
				}
			}

			.woocommerce-product-gallery__image {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0;
				padding: 0 0 0 10px;
				box-sizing: border-box;

				&:first-of-type {
					width: 100%;
				}
			}

			&.woocommerce-product-gallery--with-images {

				.woocommerce-product-gallery__image {

					&:not(:first-of-type) {
						float: left;
						padding-top: 20px;
					}
				}
			}

			$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
			@for $i from 0 to length($thumb_3_columns_label) {
				&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
					$column_width: 100%/($i+2);

					.woocommerce-product-gallery__image {

						&:not(:first-of-type) {
							width: $column_width;
						}
					}
				}
			}
	    }
    }
	
    .eltdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 50%;
        padding: 0 0 0 40px;
        float: left;
        box-sizing: border-box;
	
	    .eltdf-single-product-title {
		    margin: 3px 0;
	    }

	    h2.eltdf-single-product-title {
		    margin-top: -7px;
	    }
		div[itemprop="offers"] {
			position: relative;
			display: block;
		}

		.price {
			position: relative;
			display: block;
			margin: 0 0 32px;
			line-height: 1;
			color: $first-main-color;
			font-size: 20px;
			font-weight: 400;

			* {
				font-size: inherit;
				line-height: inherit;
			}

			del {
				margin: 0 5px 0 0;
				font-size: 15px;
				color: $default-text-color;
			}

			ins {
				text-decoration: none;
			}
		}
	
	    .woocommerce-product-rating {
		    @include eltdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 0 0 20px;

			.woocommerce-review-link {
				display: inline-block;
				color: $default-text-color;
				padding-left: 10px;
			}
	    }
	
	    div[itemprop="description"],
		.woocommerce-product-details__short-description {
		    @include eltdfRelativeHolderLayout();
		    margin: 0 0 52px;

		    p {
			    margin: 0;
		    }
	    }

		form.cart,
		p.cart{
			@include eltdfRelativeHolderLayout();
			margin: 0 0 55px;

			.select2-container--default{
				float: none;
			}
		}

		.eltdf-single-product-share-wish {
			position: relative;
			display: inline-block;
			width: 100%;
			vertical-align: middle;
			margin: 0 0 40px;

			.clear{
				display: none;
			}

			> div{
				padding: 0 30px;

				&:after{
					content: "|";
					color: $default-grey-color;
					position: absolute;
					left: -5px;
					top: 0;
				}

				&:first-child{
					padding-left: 0;

					&:after{
						display: none;
					}
				}

				&:last-child{
					padding-right: 0;
				}
			}

			.eltdf-woo-social-share-holder {
				float: left;
				position: relative;
				display: inline-block;
				vertical-align: middle;
				line-height: 1em;

				> span {
					font-size: 13px;
					text-transform: uppercase;
					letter-spacing: 2.5px;
					color: $first-main-color;
					padding: 0 15px 0 0;
					font-weight: 600;
				}
			}
		}

		.product_meta {
			> span {
				display: block;
				color: $first-main-color;
				font-weight: 400;
				padding: 5px 0;

				span, 
				a {
					color: $default-text-color;
					font-weight: 400;
				}

				a:hover {
					color: $first-main-color;
				}
			}
		}
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: 600;
		    margin: 0 0 18px;
	    }
	

    }

	.woocommerce-tabs {
		@include eltdfRelativeHolderLayout();
		margin: -62px 0 0;

		ul.tabs {
			@include eltdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			clear: both;

			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				float: left;
				border-bottom: 0;

				&:not(:first-child) {
					border-left-width: 0px;
				}

				> a {
					position: relative;
					display: inline-block;
					vertical-align: middle;
					font-size: 13px;
					letter-spacing: 2px;
					font-weight: 600;
					color: $first-main-color;
					padding: 16px 33px;
					text-transform: uppercase;
				}

				&.active {
					a {
						border: 1px solid $default-border-color;
						border-bottom: 0;

						&:after{
						    content: "";
						    display: block;
						    width: 100%;
						    position: absolute;
						    background: white;
						    height: 2px;
						    top: 100%;
						    left: 0;
						}
					}
				}
			}
		}

		.entry-content {
			padding: 23px 34px 26px;
			border: 1px solid $default-border-color;
			background-color: #fff;

			&.woocommerce-Tabs-panel--description h2,
			&.woocommerce-Tabs-panel--additional_information h2,
			&.woocommerce-Tabs-panel--reviews h2 {
				display: none;
			}
		}

		table {
			margin: 0 0 20px;

			tr, th {
				border: 0;
			}

			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;

				p {
					margin: 0;
				}
			}

			th {
				font-weight: 400;
				width: 1%;
				color: #000;
    			font-weight: 400;
    			padding: 5px 4px 5px 3px;
			}

			td {
				width: 99%;
				font-weight: 400;
			}
		}

		#reviews {

			ol.commentlist {
				list-style: none;

				li {
					padding-top: 10px;

					&:first-child {
						padding: 0;
					}
				}

				.comment_container {

					@include eltdfRelativeHolderLayout();

					img {
						float: left;
						width: 60px;
					}

					.comment-text {
						padding: 0 0 0 80px;
						margin-top: -7px;

						.star-rating {
							margin: 0;
						}

						p {
							display: block;
							margin: 5px 0 0;
						}
					}
				}
			}

			.comment-respond {
				margin-top: 15px;

				.comment-reply-title {
					color: $default-heading-color;
					margin: 0 0 10px;
					font-size: 14px;
					font-weight: 600;
					letter-spacing: 2px;
					line-height: 36px;
					text-transform: uppercase;
				}

				.stars {
					overflow: hidden;
					padding: 0;
					margin: 0;
				}

				.stars a {
					position: relative;
					display: inline-block;
					color: transparent;
					font-size: 0;
					margin: 0;

					&:before {
						position: relative;
						display: inline-block;
						vertical-align: middle;
						font-family: "ElegantIcons";
						content: "\e031";
						font-size: 12px;
						line-height: 14px;
						letter-spacing: 3px;
						overflow: hidden;
						color: $first-main-color;
						-webkit-font-smoothing: antialiased;
						-moz-osx-font-smoothing: grayscale;
					}

					&.active:after {
						position: absolute;
						right: 0px;
						bottom: 6px;
						display: block;
						font-family: "ElegantIcons";
						font-size: 12px;
						line-height: 1;
						letter-spacing: 3px;
						color: $first-main-color;
						content: "\e033\e033\e033\e033\e033";
						-webkit-font-smoothing: antialiased;
						-moz-osx-font-smoothing: grayscale;
					}
				}

				.comment-form-comment label {
					display: block;
					margin: 0 0 10px;
				}

				.comment-form-comment {
					#comment {
						width: 50%;
					}
				}

				.comment-form-author,
				.comment-form-email {
					input {
						width: 50%;
					}

					label {
						display: block;
					}
				}

				.comment-form-author {
					input {
						padding: 15px 20px;
					}
				}

				.form-submit {
					margin-top: 20px;
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 46px 0 20px;

		> h2 {
			display: block;
			margin: 32px 0 15px;
			font-size: 11px;
   		 	line-height: 2.75em;
			font-weight: 300;
			text-transform: uppercase;
			letter-spacing: 2px;
			color: $default-text-color;
			font-family: $default-text-font;
		}
		
		.product {
			
			.eltdf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */