/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .eltdf-content,
div.woocommerce {

    .sold-individually .eltdf-quantity-buttons {
        display: none;
    }

    .eltdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        background-color: #fff;
        border: 1px solid $default-border-color;
        box-sizing: border-box;
        line-height: 50px;
        height: 50px;
        padding: 0 10px 0 16px;
        margin: 0px -4px 0 0;

        .eltdf-quantity-label,
        .eltdf-quantity-input,
        .eltdf-quantity-minus,
        .eltdf-quantity-plus {
            background-color: transparent;
            border: 0;
            text-align: center;
        }

        .eltdf-quantity-label{
            margin-right: 47px;
            position: relative;
            top: -1px;
        }

        .eltdf-quantity-input {
            position: relative;
            width: 27px;
            padding: 0;
            margin: 0;
            font-family: inherit;
            outline: 0;
        }

        .eltdf-quantity-minus,
        .eltdf-quantity-plus {
            position: relative;
            border-left: 0;
            cursor: pointer;
            color: $first-main-color;
            font-size: 12px;
            font-weight: 600;
        }
    }
}

.woocommerce-page .eltdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        position: relative;
        width: 100%;
        padding: 30px 20px;
        margin: 3px 0 14px;
        font-family: inherit;
        font-size: 15px;
        line-height: 18px;
        font-weight: inherit;
        color: $default-text-color;
        background-color: #fff;
        border: 1px solid $default-border-color;
        outline: 0;
        -webkit-appearance: none;
        box-sizing: border-box;
        height: 50px;
        border-radius: 0;
    
        @include eltdfPlaceholder {
            color: $default-text-color;
        }

        &:focus{
            background: #fafafa;
        }
    }

    label{
        margin-bottom: 6px;
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */