/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.eltdf-pl-holder {
    @include eltdfRelativeHolderLayout();

	.eltdf-prl-loading{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 2;
		background-color: #fff;
		display: none;
		margin-top: 45px;
		box-sizing: border-box;
		text-align: center;
		padding: 120px;

		.eltdf-prl-loading-msg{
			font-weight: 600;
			font-size: 12px;
			letter-spacing: 2px;
			text-transform: uppercase;
			color: $first-main-color;
		}
	}

	.eltdf-pl-categories{
		position: relative;
		padding-bottom: 48px;

		.eltdf-pl-categories-label {
			display: none;

			&:after {
				content: "\43";
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-family: "ElegantIcons";
				font-size: 14px;
				line-height: 1em;
				margin-left: 8px;
				top: -1px;
			}
		}

		ul{
			list-style: none;
			//margin: 0 0 35px;
			display: inline-block;
			width: 100%;
			margin: 0;
			text-align: center;

			li {
				display: inline-block;
				position: relative;
				padding: 0 23px;

				&:first-child{
					padding-left: 0;
				}

				a{
					font-weight: 600;
					font-size: 13px;
					letter-spacing: 1.3px;
					text-transform: uppercase;
					@include eltdfTransform(color 0.3s ease);


					&:hover, 
					&.active {
						color: $default-text-color;
					}


				}
			}
		}
	}

	.eltdf-pl-ordering-outer{
		position: absolute;
		cursor: pointer;
		top: 0;
		right: 0;

		h6{
			&:after{
				content: "\e874";
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-family: "Linearicons-Free";
				font-size: 12px;
				line-height: 1em;
				margin-left: 8px;
			}
		}

		.eltdf-pl-ordering{
			background-color: #fff;
			border: 1px solid $default-border-color;
			display: inline-block;
			padding: 40px 30px 20px;
			position: absolute;
			right: 0;
			top: 46px;
			width: 365px;
			z-index: 100;
			opacity: 0;
			visibility: hidden;
			@include eltdfTransition(visibility 0s ease 0.3s, opacity 0.3s ease);

			div{
				display: inline-block;
				width: 140px;
				margin: 0 20px;

				.eltdf-pl-ordering-title {
					font-size: 13px;
				    line-height: 1.1em;
				    text-transform: uppercase;
				    font-weight: 600;
				    letter-spacing: 1.5px;
				    color: $default-heading-color;
				}

				ul {
					list-style: none;

					li {
						position: relative;
						padding: 7px 0;

						a{
							color: $default-text-color;
							@include eltdfTransform(color 0.3s ease);

							&:hover, &.active{
								color: $first-main-color;
							}

						}
					}
				}
			}
		}

		&:hover{
			.eltdf-pl-ordering{
				visibility:visible;
				opacity:1;
				transition-delay: 0s;
				transition-duration: 0.3s;
			}
		}
	}

	.eltdf-pl-outer{
		position: relative;
		z-index: 1;
		clear: both;

		.eltdf-no-posts{
			margin: 20px 15px 40px 15px;
		}
	}

    .eltdf-pli {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        box-sizing: border-box;

	    .eltdf-pli-title {
		    margin: 5px 0;
			text-transform: uppercase;
	    }

	    .eltdf-pli-category {
		    margin: -2px 0 5px 0;
	    }

        .eltdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 0px;
        }

	    .eltdf-pli-rating-holder {
		    @include eltdfRelativeHolderLayout();
		    line-height: 1;
		    margin: -5px 0 10px;
	    }

	    .eltdf-pli-rating {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			font-family: "ElegantIcons";
			font-size: 12px;
			line-height: 1.1em;
			letter-spacing: 6px;
			color: $first-main-color !important;
			overflow: hidden;
			-webkit-font-smoothing: antialiased;
			margin-top: -5px;

			&:before {
				content: "\e031\e031\e031\e031\e031";
			}

			span {
				position: absolute;
				top: 0;
				left: 0;
				overflow: hidden;

				&:before {
					content: "\e033\e033\e033\e033\e033";
				}
			}
	    }

        .eltdf-pli-price {

            * {
                font-size: inherit;
                line-height: inherit;
            }

            del {
	            margin: 0 5px 0 0;
            }

            ins {
                text-decoration: none;
            }
        }

		.eltdf-pli-add-to-cart{
			font-size: 11px;
			font-weight: 400;
			text-transform: uppercase;
			position: absolute;
			top: 50%;
			left: 50%;
			@include eltdfTransform(translate(-50%, -50%));
			letter-spacing: 2px;
			opacity: 0;
			@include eltdfTransition(all 0.3s ease-in-out);
			display: inline-block;
			background-color: rgba(255,255,255,0.8);
			padding: 15px 25px 13px;

			a{
				color: $default-heading-color;
				white-space: nowrap;

				&.eltdf-read-more-button{
					position: relative;
					display: block;
				}

				.eltdf-add-to-cart-icon {
					display: inline-block;
					vertical-align: middle;
					margin-left: 9px;
					font-size: 25px;
					pointer-events: none;
				}
			}

			.button,
			.added_to_cart {
				margin: 16px 0 0;
			}

			.added {
				display: none;
			}
		}

		&:hover{
			.eltdf-pli-text {
            	@include eltdfTransition(all .6s cubic-bezier(0.12, 0.68, 0.43, 0.95));
				opacity: 1;
			}
			.eltdf-pli-add-to-cart{
				opacity: 1;
			}
		}
    }

    .eltdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        .touch & {
            cursor: pointer;
        }

        .eltdf-pli-image {
            @include eltdfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .eltdf-pli-onsale,
            .eltdf-pli-out-of-stock,
            .eltdf-pli-new-product {
                position: absolute;
                display: block;
                top: 15px;
				right: 15px;
                width: 58px;
                height: 58px;
                border-radius: 100%;
				font-family: $alternative-text-font;
                font-size: 18px;
                line-height: 58px;
                font-style: italic;
                text-align: center;
                z-index: 100;
                @include eltdfTransform(translateZ(0));
                box-sizing: border-box;

            }

            .eltdf-pli-out-of-stock,
            .eltdf-pli-new-product {
            	background-color: $first-main-color;
            	color: #fff;
            }

            .eltdf-pli-onsale {
            	background-color: $first-main-color;
            	color: #fff;
            }
        }

        .eltdf-pli-text {
            position: absolute;
            bottom: 0;
            left: 0;
            display: inline-block;
            width: 100%;
            opacity: 0;
            box-sizing: border-box;
            @include eltdfTransition(all .3s);
			z-index: 10;
			white-space: nowrap;
        }

        .eltdf-pli-text-inner {
			font-size: 0; //to remove space between inline-block items


            .eltdf-pli-category,
            .eltdf-pli-excerpt,
            .eltdf-pli-rating,
			.eltdf-pli-price{
                color: #999999;
            }
        }

	    .eltdf-pli-link {
		    position: absolute;
		    top: 0;
		    left: 0;
		    display: inline-block;
		    width: 100%;
		    height: 100%;
			z-index: 9;
	    }
    }

	&.eltdf-info-on-image{
		.eltdf-pli-inner .eltdf-pli-text{
			padding: 10px;
			background-color: rgba(255, 255, 255, 0.9);
			@include eltdfTransform(none);
			height: 100%;
			width: 100%;
			left: 0;
			top: 0;
			z-index: 8;

			.eltdf-pli-text-outer {
				position: relative;
				display: table;
				width: 100%;
				height: 100%;
			}

			.eltdf-pli-text-inner {
				position: relative;
				display: table-cell;
				width: 100%;
				height: 100%;
				vertical-align: middle;
				text-align: center;
				font-size: 15px;
				line-height: 1em;
			}

			.eltdf-pli-price{
				@include eltdfTransform(none);
				opacity: 1;
			}
		}

		&:not(.eltdf-product-info-light) {
			.eltdf-pli-category,
			.eltdf-pli-excerpt,
			.eltdf-pli-price,
			.eltdf-pli-rating {
				color: $default-text-color;
			}
		}
	}

	&.eltdf-info-below-image {
		.eltdf-pli-text-wrapper.eltdf-no-rating-price {
			padding-bottom: 25px;
		}

		.eltdf-pli {
			.eltdf-pli-text-outer {
		    	overflow: hidden;
			} 

			.eltdf-pli-text-inner {
			    @include eltdfTransition(all .2s ease-in-out);
			    @include eltdfTransform(translateY(100%));
			}

			.eltdf-pli-text-wrapper {
				.eltdf-pli-price {
					line-height: 1em;
				}


			}

			&:hover {
				.eltdf-pli-text-inner {
				    @include eltdfTransform(translateY(0));
				}
			}
		}

		&.eltdf-product-info-button-dark {
			.eltdf-pli .eltdf-pli-add-to-cart {
				background: #000;

				a {
					color: #fff;
				}
			}
		}
	}


	.eltdf-pli-text-wrapper {
        @include eltdfRelativeHolderLayout();
        margin: 11px 0;
        text-align: center;
    }

	&.eltdf-product-info-light {

		.eltdf-pli-inner {

			.eltdf-pli-text-inner {

				.eltdf-pli-title,
				.eltdf-pli-category,
				.eltdf-pli-excerpt,
				.eltdf-pli-rating {
					color: #fff;
				}

				.eltdf-pli-price {
					color: #fff;

					del {
						color: #ccc;
					}
				}
			}
		}
	}

	&.eltdf-product-info-hover-dark {

		&.eltdf-info-on-image {

			.eltdf-pli-inner {

				.eltdf-pli-text {
					background-color: rgba(0, 0, 0, 0.8);
					padding: 0;
				}
			}
		}
	}

	
	&.eltdf-product-info-dark {
		
		.eltdf-pli-inner {
			
			.eltdf-pli-text-inner {
				
				.eltdf-pli-title,
				.eltdf-pli-category,
				.eltdf-pli-excerpt,
				.eltdf-pli-rating {
					color: $default-heading-color;
				}
				
				.eltdf-pli-price {
					color: $default-heading-color;
				}
			}
		}
	}
	
	$columns_space_label: ('no', 'tiny', 'small', 'normal', 'large');
	$columns_space_width: (0, 5, 10, 15, 25);
	
	@for $i from 0 to length($columns_space_label) {
		&.eltdf-#{nth($columns_space_label,$i+1)}-space {
			$column_width: nth($columns_space_width,$i+1);
			$column_width_padding: $column_width * 2 + 7;
			
			.eltdf-pl-outer {
				@if($column_width == 0) {
					margin: 0;
				} @else {
					margin: 0 -#{$column_width}px;
				}
				
				.eltdf-pli {
					padding: 0 #{$column_width}px #{$column_width_padding}px;
				}
			}
		}
	}
	
	/*************** Standard Layout - begin ***************/
	
	&.eltdf-standard-layout {
		
		.eltdf-pli {
			float: left;
		}
		
		&.eltdf-normal-space {
			margin: 0 0 -30px;
		}
		
		&.eltdf-small-space {
			margin: 0 0 -20px;
		}
		
		&.eltdf-tiny-space {
			margin: 0 0 -10px;
		}

		&.eltdf-info-on-image {
			.eltdf-pli-inner {
				img { 
					@include eltdfTransition(all .4s ease);
					@include eltdfTransform(scale(1.001));
				}

				&:hover {
					img { 
						@include eltdfTransform(scale(1.02));
					}
				}
			}
		}

		$standard_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($standard_list_columns) {
			&.eltdf-#{nth($standard_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.eltdf-pli {
					width: $column_width;
					
					@media only screen and (min-width: $ipad-landscape-plus-pixel) {
						
						&:nth-child(#{$i+2}n+1) {
							clear: both;
						}
					}
				}
			}
		}
	}
	
	/*************** Standard Layout - end ***************/
	
	/*************** Masonry Layout - begin ***************/
	
	&.eltdf-masonry-layout {
		
		.eltdf-pl-outer {
			opacity: 0;
			overflow: hidden; //because of the ajax loader
		}
		
		.eltdf-pl-sizer {
			width: 100%;
		}
		
		.eltdf-pl-gutter {
			width: 0;
		}
		
		.eltdf-pli-inner {
			width: 100%;
			height: 100%;

			.eltdf-pli-image {
				overflow: hidden;
				height: 100%;

				img {
					height: 100%;
					left: 50%;
					max-width: none;
					position: relative;
					@include eltdfTransform(translateX(-50%));
					width: auto;
					@include eltdfTransition(all .4s);
					-webkit-transform-origin: 50% 80%;
					transform-origin: 50% 80%;
				}
			}
		}
		
		.eltdf-pli:hover img {
			@include eltdfTransform(translateX(-50%) scale(1.02));
		}

		$masonry_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($masonry_list_columns) {
			&.eltdf-#{nth($masonry_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.eltdf-pli,
				.eltdf-pl-sizer {
					width: $column_width;
				}
				
				.eltdf-pli.eltdf-woo-image-large-width-height,
				.eltdf-pli.eltdf-woo-image-large-width{
					width: $column_width*2;
				}
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}

.eltdf-pl-holder.eltdf-normal-space.eltdf-info-on-image .eltdf-pl-outer .eltdf-pli {
	padding: 0 15px 30px;
}

/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */