/* ==========================================================================
   Product Info shortcode styles - begin
   ========================================================================== */
    .eltdf-product-info-minimal {
    	text-align: center;
    	
    	.eltdf-pi-image {
    		margin-bottom: 20px;
    		display: inline-block;
    	}

    	.eltdf-pi-excerpt {
    		margin-top: -7px;
    		p {
    			font-family: $alternative-text-font;
    			font-style: italic;
    			font-size: 20px;
    		}
    	}

    	.eltdf-pi-price {
    		span {
    			color: $first-main-color;
    			font-family: $alternative-text-font;
    			font-size: 20px;
    			margin-top: 5px;
    			margin-bottom: 12px;
    			display: inline-block;
    		}
    	}
    }
/* ==========================================================================
   Product Info shortcode styles - end
   ========================================================================== */