/* ==========================================================================
   WooCommerce Cart page styles - begin
   ========================================================================== */

.eltdf-woocommerce-page.woocommerce-cart {

	.woocommerce > form{
		float: left;
		margin-bottom: 100px;
		width: 65%;

		h3{
			margin-top: 28px;
		}

		table.cart {

			thead {
				display: none;
			}

			tbody {

				tr {
					border: 0;
				}
			}

			tr.cart_item {
				border-bottom: 1px solid $default-border-color;

				td {
					padding: 22px 0;
					vertical-align: middle;
					text-align: center;

					&.product-remove {

						padding-right: 14px;

						a {
							color: $default-text-color;
							font-size: 0;

							&:after {
								content: "\f404";
								display: inline-block;
								vertical-align: middle;
								speak: none;
								color: inherit;
								font-family: "Ionicons";
								font-size: 25px;
								font-style: normal;
								font-weight: normal;
								font-variant: normal;
								text-transform: none;
								text-rendering: auto;
								line-height: 1;
								-webkit-font-smoothing: antialiased;
								-moz-osx-font-smoothing: grayscale;
							}

							&:hover {
								color: $default-heading-color;
							}
						}
					}

					&.product-thumbnail {
						width: 92px;
						padding-left: 8px;
						padding-right: 8px;

						a, img {
							display: block;
						}
					}

					&.product-name {
						text-align: left;
						padding-left: 20px;

						a {
							display: inline-block;
							font-size: 17px;
							line-height: 23px;
							font-weight: 400;
							letter-spacing: 2px;
							text-transform: uppercase;
							font-family: $alternative-text-font;
						}
					}

					&.product-quantity {
						text-align: right;
					}

					&.product-price,
					&.product-subtotal{
						font-size: 15px;
						font-family: $default-text-font;
					}

					&.product-subtotal{
						font-weight: 400;
						display: none;
					}

					.variation {

						> * {
							display: inline-block;
							vertical-align: middle;
							padding: 0;
							margin: 0;
						}

						p {
							margin: 0;
						}
					}
				}
			}

			td.actions {

				padding: 0;
				border-bottom: 1px solid $default-border-color;

				.coupon {
					position: relative;
					display: inline-block;
					vertical-align: middle;
					float: left;
					background-color: #f7f6f5;
					box-sizing: border-box;
					padding-right: 10px;
					width: 50%;
					text-align: left;

					label {
						display: inline-block;
						vertical-align: middle;
						font-size: 0;

						&:after {
							content: "P";
							position: relative;
							display: inline-block;
							vertical-align: top;
							font-family: 'linea-ecommerce-10';
							font-size: 20px;
							line-height: inherit;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
							width: 50px;
							height: 50px;
							line-height: 50px;
							text-align: center;
						}
					}

					input[type="text"] {
						border: 0;
						width: calc(100% - 175px);
						margin: 0;
						font-size: 15px;
						padding: 0;
						background-color: transparent;
					}

					input[type="submit"]{
						float: right;
					}
				}

				input[type="submit"],
				button[type="submit"]{
					background-color: transparent;
					border: 0;
					padding: 0;
					margin: 0;
					font-size: 11px;
					font-weight: 400;
					text-transform: uppercase;
					letter-spacing: 2px;
					font-family: $default-text-font;
					color: $default-heading-color;
					cursor: pointer;
					line-height: 54px;
					height: 54px;
				}


				> input {
					float: right;
				}
			}
		}

		.eltdf-cart-go-back{
			display: inline-block;
			position: relative;
			font-size: 15px;
			font-weight: 300;
			line-height: 22px;
			color: $default-text-color;
			margin-top: 35px;

			&:before {
				content: "\e879";
				position: relative;
				display: inline-block;
				vertical-align: top;
				font-family: 'Linearicons-Free';
				font-size: 18px;
				line-height: inherit;
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
				margin-right: 10px;

			}
		}
	}
	
	.cart-collaterals {
		float: right;
		margin-bottom: 100px;
		padding: 0;
		width: 30%;
		padding: 28px;
		box-sizing: border-box;
		background-color: #f7f6f5;

		
		h2 {
			position: relative;
			display: block;
			font-size: 20px;
		    line-height: 2em;
		    letter-spacing: 2px;			
		    margin-top: 0;
			margin-bottom: 21px;
			font-weight: 400;
		}
		
		table {
			
			tr {
				border: 0;
				border-top: 1px solid $default-border-color;
			}
			
			td, th {
				padding: 8px 0;
				text-align: left;
				vertical-align: top;
			}
			
			th {
				padding-right: 30px;
				border: 0;
				font-size: 15px;
				font-weight: 400;
				color: $default-heading-color;
				text-transform: uppercase;
			}

			td{
				font-size: 15px;
				font-family: $default-text-font;
			}
		}

		tr.cart-subtotal{
			border: 0;
		}

		tr.shipping {
			border: 0;
			
			#shipping_method {
				list-style: none;
				margin: -5px 0 7px;
				
				li {
					margin: 3px 0;
					padding: 0;
					
					> * {
						display: inline-block;
						vertical-align: middle;
						margin: 0;
					}

					label{
						font-size: 15px;
						color: $default-text-color;
						font-weight: 300;
						font-family: $default-text-font;
					}
				}
			}
		}

		tr.order-total{
			th,td{
				padding-top: 20px;
				padding-bottom: 20px;
			}

			th {
				font-family: $alternative-text-font;
				font-size: 17px;
			}

			td{
				font-size: 20px;
				color: $default-heading-color;
				text-align: right;
			}

			.amount {
				font-weight: 400 !important;

			}
		}
		
		.woocommerce-shipping-calculator {
			margin-top: 25px;

			.shipping-calculator-form{
				margin-bottom: 50px;
			}

			.shipping-calculator-button {
				display: block;
				font-family: $alternative-text-font;
				letter-spacing: 2px;
				text-transform: uppercase;
				font-weight: 400;
				margin:0 0 22px;
				font-size: 16px;

				&:after {
				    content: "\e874";
				    position: relative;
				    display: inline-block;
				    vertical-align: middle;
				    font-family: Linearicons-Free;
				    font-size: 12px;
				    line-height: 1em;
				    margin-right: 6px;

				}
			}
			
			button {
				height: 50px;
				line-height: 46px;
				padding: 0px;
				width: 100%;
				background-color: #000;
				text-align: center;
				color: #fff;
				@include eltdfTransition(background-color 0.3s ease);

				&:hover{
					background-color: rgba(0,0,0,0.75);
				}
			}
			
			p {
				margin: 0;
			}

			.select2-container{
				margin: 0 0 7px;

			}

			input[type="text"]{
				padding-top: 15px;
				padding-bottom: 15px;
				margin-bottom: 7px;
			}
		}

		.checkout-button{
			width: 100%;
			text-align: center;
		}
	}
	
	.cross-sells {
		margin: 1px 0 0;
		
		ul.products  {
			
			> .product {
				
				.eltdf-pl-text-wrapper {
					margin: 0 0 17px;
				}
			}
		}
	}
	
	.cart_totals {
		margin: 0 0 10px;
	}
}

.eltdf-woocommerce-page {
	
	.cart-empty {
		position: relative;
		display: block;
		font-family: $alternative-text-font;
		font-size: 30px;
		line-height: 1em;
		text-align: center;
		color: $default-heading-color;
		letter-spacing: 3px;
		font-weight: 400;
		text-transform: uppercase;
		margin-bottom: 28px;
	}

	.cart-empty + p {
		text-align: center;
		padding: 0 23%;
		font-family: $alternative-text-font;
		font-size: 20px;
	    line-height: 33px;
	    font-style: italic;
	}
	
	.return-to-shop {
		position: relative;
		display: block;
		text-align: center;
		margin: 40px 0 100px;
		a.button.wc-backward {
			padding: 11px 43px;
		}
	}
}
/* ==========================================================================
   WooCommerce Cart page styles - end
   ========================================================================== */