/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.eltdf-woo-pagination-holder {
    @include eltdfRelativeHolderLayout();
    margin: 0 0 35px;
    clear: both;

    .eltdf-woo-pagination-inner {
        position: relative;
        display: table;
        width: 100%;
        text-align: right;
    }
}

.woocommerce-pagination {
    @include eltdfRelativeHolderLayout();
    clear: both;
    padding-top: 41px;
    border-top: 1px solid $default-border-color;

    .page-numbers {
         @include eltdfRelativeHolderLayout();
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            padding: 0;

            a,
            span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                padding: 0 2px;
                color: $first-main-color;
                font-size: 13px;
                font-weight: 600;
                line-height: 40px;
                width: auto;
                margin: 0 12px;
                text-decoration: none;
                cursor: pointer;
                white-space: nowrap;

                &.current {
                    &:after {
                        content: '';
                        background-color: $first-main-color;
                        width: 100%;
                        height: 1px;
                        position: absolute;
                        top:100%;
                        left: 1px;
                        display: inline-block;
                    }
                }
            }

            a {

                &.next,
                &.prev {
                    font-size: 0;
                    margin: 0 6px 0 4px;
                    font-family: 'Linearicons-Free';

                    &:before {
                        display: block;
                        font-size: 18px;
                        font-weight: 500;
                        line-height: inherit;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                    }
                }

                &.prev {

                    &:before {
                        content: "\e879";
                    }
                }

                &.next {

                    &:before {
                        content: "\e87a";
                    }
                }
            }
        }
    }
}

.eltdf-woo-view-all-pagination {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;

    a {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        white-space: nowrap;
        padding: 0 0 0 2px;
        color: $first-main-color;

        &:before {
            content: '|';
            padding: 0 14px 0 0;
            color: $first-main-color;
        }

        &:hover {
            color: $first-main-color;
        }
    }
}
/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */