/* ==========================================================================
   Product List - Carousel shortcode styles - begin
   ========================================================================== */

.eltdf-plc-holder {
    @include eltdfRelativeHolderLayout();

    .eltdf-plc-outer {
        @include eltdfRelativeHolderLayout();
    }

    .eltdf-plc-item {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        overflow: hidden;
        box-sizing: border-box;

        &:hover {

            .eltdf-plc-image-outer {

                .eltdf-plc-text {
                    opacity: 1;
                }
            }
            
            .eltdf-plc-add-to-cart{
                opacity: 1;
            }

            .eltdf-plc-text-inner {
                @include eltdfTransform(translateY(0));
            }
        }

        .touch & {
            cursor: pointer;
        }

        .eltdf-plc-image-outer {
            @include eltdfRelativeHolderLayout();

            .eltdf-plc-image {
                @include eltdfRelativeHolderLayout();

                img {
                    display: block;
                    width: 100%;
                }

                .eltdf-plc-onsale,
                .eltdf-plc-out-of-stock,
                .eltdf-plc-new-product {
                    position: absolute;
                    display: block;
                    top: 15px;
                    right: 15px;
                    width: 58px;
                    height: 58px;
                    border-radius: 100%;
                    font-family: $alternative-text-font;
                    font-size: 18px;
                    line-height: 58px;
                    font-style: italic;
                    text-align: center;
                    z-index: 100;
                    @include eltdfTransform(translateZ(0));
                    box-sizing: border-box;
                }

                .eltdf-plc-out-of-stock,
                .eltdf-plc-new-product {
                    background-color:$first-main-color;
                    color: #fff;
                }

                .eltdf-plc-onsale {
                    background-color: $first-main-color;
                    color: #fff;
                }
            }

            .eltdf-plc-text {
                position: absolute;
                bottom: 0;
                display: inline-block;
                width: 100%;
                opacity: 0;
                box-sizing: border-box;
                @include eltdfTransition(all 0.3s ease);
                z-index: 10;
                white-space: nowrap;
            }

            .eltdf-plc-text-inner {
                font-size: 0; //to remove space between inline-block items


                .eltdf-plc-category,
                .eltdf-plc-excerpt,
                .eltdf-plc-rating,
                .eltdf-plc-price{
                    color: #999999;
                }
            }
        }

        .eltdf-plc-text-outer {
            overflow: hidden;
        }

        .eltdf-plc-text-inner {
            @include eltdfTransition(all .2s);
            @include eltdfTransform(translateY(100%));
        }

        .eltdf-plc-title {
            margin: 0;
        }

        .eltdf-plc-category {
            margin: 0px;
        }

        .eltdf-plc-excerpt {
            color: $default-heading-color;
            line-height: 1.3em;
            margin: 0px;
        }

        .eltdf-plc-rating-holder {
            @include eltdfRelativeHolderLayout();
            line-height: 1;
            margin: 3px 0 6px;
        }

        .eltdf-plc-rating {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            font-family: "ElegantIcons";
            font-size: 12px;
            line-height: 1.1em;
            letter-spacing: 6px;
            color: #ebbc00;
            overflow: hidden;
            -webkit-font-smoothing: antialiased;

            &:before {
                content: "\e031\e031\e031\e031\e031";
            }

            span {
                position: absolute;
                top: 0;
                left: 0;
                overflow: hidden;

                &:before {
                    content: "\e033\e033\e033\e033\e033";
                }
            }
        }

        .eltdf-plc-price {
            display: block;
            color: #999;
            position: relative;
            margin-top: -5px;

            del {
                margin: 0 5px 0 0;
            }

            ins {
                text-decoration: none;
            }
        }

        .eltdf-plc-add-to-cart{
            font-size: 11px;
            font-weight: 400;
            text-transform: uppercase;
            position: absolute;
            top: 50%;
            left: 50%;
            @include eltdfTransform(translate(-50%, -50%));
            letter-spacing: 2px;
            opacity: 0;
            @include eltdfTransition(all 0.3s ease);
            display: inline-block;
            background-color: rgba(255,255,255,0.8);
            padding: 15px 25px 13px;

            a{
                color: $default-heading-color;
                white-space: nowrap;

                &.eltdf-read-more-button{
                    position: relative;
                    display: block;
                }

                .eltdf-add-to-cart-icon {
                    display: inline-block;
                    vertical-align: middle;
                    margin-left: 9px;
                    font-size: 25px;
                }
            }

            .button,
            .added_to_cart {
                margin: 16px 0 0;
            }

            .added {
                display: none;
            }
        }

        .eltdf-plc-text-wrapper {
            @include eltdfRelativeHolderLayout();
            margin: 12px 0 38px;
            text-align: center;
        }
    }

    .eltdf-owl-slider .owl-nav{
        .owl-next,
        .owl-prev {
            top: -50px;
            @include eltdfTransform(none);

            .eltdf-prev-icon,
            .eltdf-next-icon {
                font-size: 14px;
            }
        }

        .owl-prev {
            left: auto;
            right: 28px;
        }

        .owl-next{
            left: auto;
            right: -3px;
        }
    }

	&.eltdf-plc-nav-light-skin {
		
		.owl-nav {
			
			.owl-prev,
			.owl-next {
				
				&:hover {
					
					.eltdf-prev-icon,
					.eltdf-next-icon {
						color: $header-light-hover-color;
					}
				}
			}
			
			.eltdf-prev-icon,
			.eltdf-next-icon {
				color: $header-light-color;
			}
		}
	}
	
	&.eltdf-plc-pag-inside-slider {
		
		.owl-prev,
		.owl-next {
			@include eltdfTransform(translateY(-50%));
		}
		
		.owl-dots {
			position: absolute;
			left: 0;
			bottom: 10px;
			margin: 0;
		}
	}
	
	&.eltdf-plc-pag-light-skin {
		
		.owl-dots {
			
			.owl-dot {

				span {

					&:after {
						background-color: $header-light-hover-color;
					}
				}

				&:hover,
				&.active {

					span {

						&:after {
							background-color: $header-light-color;
						}
					}
				}
			}
		}
	}
}
/* ==========================================================================
   Product List - Carousel shortcode styles - end
   ========================================================================== */