/* ==========================================================================
    WooCommerce Dropdown Cart styles - start
   ========================================================================== */

.eltdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0px 10px;

    .touch & {
        cursor: pointer;
    }

    &.eltdf-cart-info-is-disabled {

        .eltdf-header-cart {

            .eltdf-cart-info {
                display: none;
            }
        }
    }

    .eltdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .eltdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        color: $default-text-color;

        .eltdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            font-size: 32px;
            line-height: 1em;
            color: $first-main-color;
        }
        .eltdf-cart-count {
            position: absolute;
            top: 50%;
            left: 50%;
            @include eltdfTransform(translate(-50%, -50%));
            color: $first-main-color;
            font-size: 11px;
            font-weight: 600;
        }

        .eltdf-cart-info {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            padding: 0 0 0 5px;
            font-size: 12px;
            line-height: 1;
            clear: both;
            font-family: $alternative-text-font;
            font-weight: 400;
            color: $default-text-color;

            .eltdf-cart-info-number {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                padding: 0 8px 0 0;
                margin: 0 5px 0 0;

                &:after {
                    position: absolute;
                    right: -1px;
                    content: '-';
                }
            }

            .eltdf-cart-info-total {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
            }
        }

        &:hover {
            color: $default-heading-color;
        }
    }

    &:hover {

        .eltdf-shopping-cart-dropdown {
            visibility: visible;
            opacity: 1;
            max-height:2000px;
        }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
     color: $header-dark-color;

     &:hover {
         color: $header-dark-hover-color;
     }
 }

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart .eltdf-cart-icon-text,
.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart .eltdf-cart-info {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart .eltdf-cart-icon-text,
.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart .eltdf-cart-info {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.eltdf-shopping-cart-widget:hover .eltdf-shopping-cart-dropdown {
    max-height: 2000px;
    visibility: visible;
}

.eltdf-shopping-cart-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    width: 350px;
    background-color: #ffffff;
    text-align: left;
    z-index: 1000;
    box-sizing: border-box;
    max-height:0;
    visibility: hidden;
    overflow:hidden;
    box-shadow: 0 5px 5px 0 rgba(0,0,0,0.03);
    @include eltdfTransition(max-height 1.5s ease-in-out);

    ul {
        margin: 0;
        padding: 33px;
        li {
            position: relative;
            display: inline-block;
            box-sizing: border-box;
            height: 100%;
            width: 100%;
            vertical-align: middle;
            margin-bottom: 15px;

            &.eltdf-empty-cart {
                border: 0;
                min-height: auto;
                margin: 0;
            }
        }
    }

    .eltdf-item-image-holder {
        position: relative;
        display: block;
        height: 100%;
        width: 75px;
        vertical-align: top;
        float: left;

        img {
            display: block;
        }
    }

    .eltdf-item-info-holder {
        position: relative;
        display: block;
        height: 100%;
        width: calc(100% - 75px);
        vertical-align: middle;
        vertical-align: top;
        padding-left: 20px;
        float: left;
        box-sizing: border-box;

        .eltdf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            color: #000;
            font-weight: 400;
            letter-spacing: 0.1em;
            line-height: 20px;
            position: relative;

        }

        .remove {
            position: absolute;
            top: -4px;
            right: -5px;
            width: 18px;
            height: 18px;
            color: #000;
	        font-size: 15px;
	        line-height: 24px;
            text-align: center;
            font-family: $alternative-text-font;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .eltdf-quantity {
            display: inline-block;
            margin: 0;
            font-family: $default-text-font;
            color: #989899;
            font-size: 15px;
            position: relative;
            top: 0;
            line-height: 27px;
            vertical-align: middle;


            &:after {
                content: 'x';
                display: inline-block;
                position: relative;
                padding-left: 3px;
            }
        }

        .amount {
            font-family:  $default-text-font;
            font-size: 15px;
            display: inline-block;
            color: #989899;
            line-height: 27px;
            position: relative;
            top: 0px;
            vertical-align: middle;
        }
    }

    .eltdf-cart-bottom {
        padding: 23px 0 43px;

        .eltdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 12px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
            }

            .eltdf-total {
                font-family: $alternative-text-font;
                font-size: 15px;
                font-weight: 400;
                color: #000;
                letter-spacing: 0.15em;
            }

            .eltdf-total-amount {
                font-family: $default-text-font;
                text-align: right;
                font-size: 15px;
                font-weight: 400;
                color: #999999;
            }
        }

        .eltdf-btn-holder {
            a {
                font-weight: 600;
                font-size: 9px;
                color: #fff;
                height: 44px;
                line-height: 44px;
                margin-left: 1px;
                margin-right: -3px;
                @include eltdfTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);

                &:hover {
                   color: #fff;
                   background-color: rgba(0,0,0,.75);
                }

            }

            &:nth-child(2) {
                float: left;
                width: 49%;
            }

             &:nth-child(3) {
                float: right;
                width: 49%;
            }

        }


        .eltdf-cart-description {
            @include eltdfRelativeHolderLayout();
            margin: 0 0 10px;

            .eltdf-cart-description-inner {
                position: relative;
                display: table;
                width: 100%;
                height: 100%;
                padding: 17px 30px;
                background-color: #f4f4f4;
                text-align: center;
                box-sizing: border-box;

                span {
                    position: relative;
                    display: table-cell;
                    width: 100%;
                    height: 100%;
                    vertical-align: middle;
                    color: $default-heading-color;
                    font-size: 12px;
                    line-height: 18px;
                }
            }
        }

        .eltdf-view-cart {
            @include eltdfRelativeHolderLayout();
            height: 44px;
            line-height: 44px;
            padding: 0 26px;
            font-size: 10px;
            font-weight: inherit;
	        letter-spacing: 2px;
            color: #fff;
            background-color: $first-main-color;
            border: 0;
            outline: none;
            cursor: pointer;
            text-align: center;
            -webkit-appearance: none;
            box-sizing: border-box;
            @include eltdfTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);

            span {
                display: block;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - start
   ========================================================================== */